<template>
	<div>
		<div class="back-title"><span>账号功能</span></div>
		<div class="pagebox" style="margin: 10px 0;">
			<Table :columns="columns1" :data="tableData3" :disabled-hover="true"></Table>
		</div>
	</div>
</template>

<script>
 export default {
   name: 'notice',
   data () {
     return {
       columns1: [
         {
           title: '账号信息',
           key: 'functionname',
           width: '100',
           className: 'noticebox'
         },
         {
           title: '状态',
           key: 'functionstast',
           className: 'noticebox',
           width: '100'
         },
         {
           title: '说明',
           key: 'condition',
           className: 'noticebox'
         },
         {
           title: '记录',
           key: 'functionurl',
           className: 'noticebox',
           width: '100',
           render: (h, params) => {
             return h('a', {
               attrs: {
                 href: params.row.functionurl,
                 target: '_blank'
               }
             }, '查看')
           }
         }
       ],
       tableData3: [
         {
           functionname: '信用分值',
           functionstast: '100分',
           condition: '违规行为会触发扣分和相应惩罚 信用分说明',
           functionurl: 'https://www.toutiao.com/i6487462122262364685/'
         }
       ]}
   },
   methods: {
     getdata (name) {
       console.log(name)
     }
   }
 }
</script>

<style>
</style>


